<template>
    <div id="intro" class="container">
            <div class="clock base">
                <img src="../assets/base.png">
            </div>
            <div class="clock min">
                <img src="../assets/min.png">
            </div>
            <div class="clock hour">
                <img src="../assets/hour.png">
            </div>
        <div class="content_1">
            <div class="partical col-md-6">
                <div class="title_block">
                    <div class="mask"></div>
                    <h1 class="title"></h1>
                </div>
                <div class ="partical_block">
                    <p></p>
                    <p></p>
                </div>
                <a href="#/QA">FAQ</a>
            </div>
        </div>

    </div>
</template>
<script>
import Parallax from 'vue-parallaxy'
import sr from '../js/sr.js';
export default {
    name: 'intro',
    components: {
        Parallax
    },
    mounted() {


        let titleAnime = this.$anime.timeline({
            easing: [0.8, 0, 0.2, 1],
            autoplay: false,
        });
        titleAnime
            .add({
                targets: '.mask',
                translateY: [37, 0],
                duration: 600,
            })
            .add({
                targets: '.mask',
                translateY: [0, -37],
                duration: 600,
                offset: '-=100'
            })
            .add({
                targets: '.title',
                opacity: [0, 1],
                translateY: [10, 0],
                duration: 800,
                easing: [0.8, 0, 0.2, 0],
                offset: '-=600'

            })
        sr.reveal('.partical_block', {
            distance: '10px',
            duration: '800',
            delay: 0,
        });
        sr.reveal('.title_block', {
            distance: '0px',
            opacity: '1',
            duration: '0',
            reset: true,
            afterReveal: function(el){
                titleAnime.play()
            }
        })
    },
}

</script>



