<template lang="html">
    <div id="staff" class="container">
      <h1><b>S</b>taff</h1>
      <div class="group" v-for="(group, groupId) in groups" :key="groupId">
        <h2>{{ group.name }}</h2>
        <StaffComp v-for="(member, memberId) in group.members" :nickname="member" :key="memberId"/>
      </div>
    </div>
</template>

<script>
import sr from '../js/sr.js';
import StaffComp from './staffComp';

export default {
    name: 'staff',
    components: { StaffComp },
    data() {
      return {
        groups: {
          managers: {
            name: '',
            members: [ '', '' ]
          },
          marketing: {
            name: '',
            members: [ '', '', '' ]
          },
          publicRelation: { 
            name: '',
            members: [ '', '', '', '' ]
          },
          activity: {
            name: '',
            members: [ '', '', '', '', '', '', '', '' ]
          },
          affair: {
            name: '',
            members: [ '', '', '' ]
          }
        }
      }
    },
    mounted() {
        var headAnime = this.$anime.timeline({
            easing: [0.8, 0, 0.2, 1],
            autoplay: false
        });
        headAnime
            .add({
                targets: '.staff_photo',
                scale: [0,1],
                delay: function(el, i) { return i * 100 },
                duration: 1200,
            })
            .add({
                targets: '.staff_photo',
                delay: function(el, i) { return i * 100 },
                borderWidth: [40, 1],
                duration: 1200,
                offset: '-=1000'
            })
            .add({
                targets: '.nickname',
                delay: function(el, i) { return i * 100 },
                opacity: [0, 1],
                translateY: [10, 0],
                duration: 1200,
                offset: '-=1000'
            })
        sr.reveal('.group', {
            duration: '0',
            afterReveal: function(){
                headAnime.play();
            }
        });
    }
}
</script>



